import { myID, ws, connect } from '../../js/network'

export function checkOrReconnect() {
  if (!ws) {
    //    
    connect()
    return false
  }
  if (ws.readyState === WebSocket.CONNECTING) return false

  if (ws.readyState === WebSocket.OPEN) {
    return true
  }
  // connect()
  return false
}
